}

nav.primary {
    background-color: #000000;
    max-height: 54px;
    border: none;
    color: #FFFFFF;
    text-align: center;
}

nav.primary ul {
    display: inline-flex;
    height: 54px;
    list-style: none;
    align-items: center;
    -ms-box-orient: horizontal;
}

nav.primary ul li.item {
    display: inline-flex;
    cursor: pointer;
    background-image: none;
    background-color: #47a3da;
    color: #FFFFFF;
    padding: 10px 20px;
    margin-right: 8px;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

nav.primary ul li.item.active {
    padding: 8px 18px;
    border: 2px solid #47a3da;
    background-color: transparent;
}

nav.primary ul li.item.active:active {
    border-color: #FFFFFF;
}

nav.primary ul li a {
    color: #FFFFFF;
}

nav.primary ul li select {
    -webkit-appearance: inherit;
    font: 16px 'Open Sans', sans-serif;
    background: transparent;
    color: #FFFFFF;
    padding: 8px 18px;
    border: 2px solid #47a3da;
    outline: none;
}

nav.primary ul li select option {
    border: 0;
    display: inherit;
    font-weight: inherit;
    padding: 4px 2px;
    background-color: #000000;
    color: #FFFFFF;
}

nav.primary ul li select option:hover, nav.primary ul li select option :focus {
    background-color: #47a3da;
}

nav li.header {
    display: inline-flex;
    margin-right: 26px;
}

nav.vertical {
    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    height: 100%;
    margin: 0;
    padding: 0;
    background: #f7f7f7;
}

nav.vertical ul {
    list-style-type: none;
    margin-left: 0;
    
}

nav.vertical ul li a {
    display: block;
    height: 4em;
    width: 4em;
    line-height: 4em;
    text-align: center;
    color: #999;
    position: relative;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    transition: background 0.1s ease-in-out;
    -webkit-transition: background 0.1s ease-in-out;
    -moz-transition: background 0.1s ease-in-out;
}

nav.vertical ul li a:hover, nav.vertical ul li a :first-child a {
    background: #47a3da;
        
    color: #FFFFFF;
}

nav.vertical ul li.active a {
    background: #fff;
    color: #47a3da;
}

footer nav {
    font-size: 1.2em;
    text-transform: capitalize;
}

footer nav ul {
    list-style-type: none;
    margin-left: 0;
}

footer nav ul li {
    display: inline-block;
    margin-right: 46px;
    position: relative;
}

footer nav ul li a {
    color: #FFFFFF;
}

footer nav ul li ul {
    position: absolute;
    display: block;
    visibility: hidden;
    background: #f7f7f7;
    transform: translateX(-26px) translateY(-20px);
    bottom: 100%;
    left: calc(50% - 6px);
    transition: visibility 0.3s;
}

footer nav ul li ul li {
    border: 4px solid transparent;
    margin: 0;
}

footer nav ul li ul li a {
    padding: 2px 8px;
    color: #47a3da;
}

footer nav ul li ul li a:hover {
    background-color: #47a3da;
    color: #FFFFFF;
}

footer nav ul li ul:after {
    content: ' ';
    position: absolute;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 6px solid #f7f7f7;
    transform: translateX(26px);
}

footer nav ul li:hover > ul {
    visibility: visible;
}

